import React from 'react'
import Head from "next/head"

import { style, media } from "typestyle"
import { clsConcat, TINT_COLOR } from "../utils"

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars } from "@fortawesome/free-solid-svg-icons"
import { faWeibo, faGithub } from '@fortawesome/free-brands-svg-icons'

import VStack from "./VStack"
import HStack from "./HStack"

const s = {
    container: style({
        width: "100%",
        padding: "30px 0",
        margin: "0 auto",
        boxSizing: "border-box",
        display: "flex",
        justifyContent: "space-between",
        transition: "box-shadow cubic-bezier(.3,0,.1,1) 800ms",
    }, media({ minWidth: 768 }, {
        maxWidth: "1200px"
    }), media({ minWidth: 320, maxWidth: 480 }, {
        padding: "15px 20px",
        position: "fixed",
        zIndex: 3,
        top: "0",
        left: "0",
        backgroundColor: "#FFFFFF"
    })),
    qin: style({
        width: "40px",
        height: "40px",
        border: "1px solid rgba(0, 0, 0, 0.4)",
        cursor: "pointer"
    }, media({ minWidth: 320, maxWidth: 480 }, {
        width: "30px",
        height: "30px",
    })),
    qinWrapper: style({
        fontSize: 0
    }),
    nav: style({

    }, media({ minWidth: 320, maxWidth: 480 }, {
        display: "none"
    })),
    menu: style({
        display: "flex",
        justifyContent: "flex-end",
        listStyleType: "none",
        padding: "0"
    }),
    bar: style({
        display: "none"
    }, media({ minWidth: 320, maxWidth: 480 }, {
        display: "block",
        width: "18px",
        height: "32px",
        color: "rgba(0, 0, 0, 0.8)",
        paddingTop: "5px",
        boxSizing: "border-box"
    })),
    menuItem: style({
        padding: "0 0 0 20px",
        letterSpacing: "0.05rem",
        textTransform: "uppercase",
        transition: "color linear 200ms",
        cursor: "pointer",
        "$nest": {
            "&:hover": {
                color: TINT_COLOR
            }
        }
    }),
    menuItemActive: style({
        color: TINT_COLOR
    }),
    menuPop: style({
        width: "100vw",
        height: "100vh",
        position: "fixed",
        left: "0",
        top: "-100vh",
        zIndex: 2,
        backgroundColor: "white",
        transition: "all cubic-bezier(.3,0,.1,1) 800ms",
        transform: "translate3d(0, 0, 0)"
    }),
    menuPopShow: style({
        transform: "translate3d(0, 100vh, 0)",
    }),
    menuPoppedContainerEffect: style({
        boxShadow: "0 4px 10px rgba(0, 0, 0, 0.1)"
    }),
    menuPopped: style({
        marginTop: "70px",
        listStyle: "none"
    }),
    menuPoppedItem: style({
        padding: "20px 0 5px 0",
        letterSpacing: "0.05rem",
        textTransform: "uppercase",
        borderBottom: "0.5px solid rgba(0, 0, 0, 0.15)",
        transition: "color linear 200ms",
        cursor: "pointer",
        "$nest": {
            "&:hover": {
                color: "#5696E7"
            }
        }
    }),
    socialContainer: style({
        paddingLeft: "40px",
        marginTop: "40px"
    }),
    socialIconWrapper: style({
        width: "2rem",
        marginRight: "1rem"
    }),
    socialIcon: style({
        color: "rgba(0, 0, 0, 0.4)",
        margin: "10px 0",
        transition: "color linear 300ms",
        "$nest": {
            "&:hover": {
                color: "rgba(0, 0, 0, 0.8)",
            }
        }
    }),
}

interface IHeaderProps {
    active?: string
}

interface IHeaderState {
    showMenu: Boolean
}

class Header extends React.Component<IHeaderProps, IHeaderState> {
    constructor(props) {
        super(props)
        this.state = {
            showMenu: false
        }
    }

    onMenuIconClick = () => {
        this.setState({ showMenu: !this.state.showMenu })
    }

    render() {
        const menu = ["home", "news", "projects", "publication", "articles", "cv"]
        return (
            <>
                <div className={clsConcat(s.menuPop, this.state.showMenu ? s.menuPopShow : "")}>
                    <ul className={s.menuPopped}>
                        {
                            menu.map((v, i) => (
                                <a key={i}
                                    href={v === "home" ? "/" : (v === "cv" ? `/cv.pdf` : `/${v}`)}
                                    target={v === "cv" ? "_blank" : ""}>
                                    <li className={clsConcat(s.menuPoppedItem, this.props.active === v ? s.menuItemActive : "")}>{v}</li>
                                </a>
                            ))
                        }
                    </ul>

                    <VStack className={s.socialContainer}>
                        <span>Find me on internet:</span>
                        <HStack>
                            <a href="https://weibo.com/Spartan093" target="_blank" className={s.socialIconWrapper}>
                                <FontAwesomeIcon className={s.socialIcon} icon={faWeibo} />
                            </a>
                            <a href="https://github.com/YiqinZhao" target="_blank" className={s.socialIconWrapper}>
                                <FontAwesomeIcon className={s.socialIcon} icon={faGithub} />
                            </a>
                        </HStack>
                    </VStack>
                </div>

                <header id="header" className={clsConcat(s.container, this.state.showMenu ? s.menuPoppedContainerEffect : "")}>
                    <Head>
                        <link rel="stylesheet" href="https://cdn.jsdelivr.net/gh/highlightjs/cdn-release@9.15.10/build/styles/default.min.css" />
                        <script src="https://cdn.jsdelivr.net/gh/highlightjs/cdn-release@9.15.10/build/highlight.min.js"></script>
                        <link rel="stylesheet" href="/css/base.css" />

                        <meta name="apple-mobile-web-app-capable" content="yes"></meta>
                        <meta name="apple-mobile-web-app-status-bar-style" content="default"></meta>
                        <meta name="apple-mobile-web-app-title" content="YiqinZhao"></meta>
                        <link href="/splashscreens/iphone5_splash.png" media="(device-width: 320px) and (device-height: 568px) and (-webkit-device-pixel-ratio: 2)" rel="apple-touch-startup-image" />
                        <link href="/splashscreens/iphone6_splash.png" media="(device-width: 375px) and (device-height: 667px) and (-webkit-device-pixel-ratio: 2)" rel="apple-touch-startup-image" />
                        <link href="/splashscreens/iphoneplus_splash.png" media="(device-width: 621px) and (device-height: 1104px) and (-webkit-device-pixel-ratio: 3)" rel="apple-touch-startup-image" />
                        <link href="/splashscreens/iphonex_splash.png" media="(device-width: 375px) and (device-height: 812px) and (-webkit-device-pixel-ratio: 3)" rel="apple-touch-startup-image" />
                        <link href="/splashscreens/iphonexr_splash.png" media="(device-width: 414px) and (device-height: 896px) and (-webkit-device-pixel-ratio: 2)" rel="apple-touch-startup-image" />
                        <link href="/splashscreens/iphonexsmax_splash.png" media="(device-width: 414px) and (device-height: 896px) and (-webkit-device-pixel-ratio: 3)" rel="apple-touch-startup-image" />
                        <link href="/splashscreens/ipad_splash.png" media="(device-width: 768px) and (device-height: 1024px) and (-webkit-device-pixel-ratio: 2)" rel="apple-touch-startup-image" />
                        <link href="/splashscreens/ipadpro1_splash.png" media="(device-width: 834px) and (device-height: 1112px) and (-webkit-device-pixel-ratio: 2)" rel="apple-touch-startup-image" />
                        <link href="/splashscreens/ipadpro3_splash.png" media="(device-width: 834px) and (device-height: 1194px) and (-webkit-device-pixel-ratio: 2)" rel="apple-touch-startup-image" />
                        <link href="/splashscreens/ipadpro2_splash.png" media="(device-width: 1024px) and (device-height: 1366px) and (-webkit-device-pixel-ratio: 2)" rel="apple-touch-startup-image" />

                        <link rel="apple-touch-icon" sizes="180x180" href="/icons/apple-touch-icon.png" />
                        <link rel="icon" type="image/png" sizes="32x32" href="/icons/favicon-32x32.png" />
                        <link rel="icon" type="image/png" sizes="16x16" href="/icons/favicon-16x16.png" />
                        <link rel="manifest" href="/site.webmanifest" />
                        <link rel="mask-icon" href="/safari-pinned-tab.svg" color="#ffffff" />
                        <link rel="shortcut icon" href="/icons/favicon.ico" type="image/x-icon" />
                        <meta name="msapplication-TileColor" content="#ffffff" />
                        <meta name="theme-color" content="#ffffff" />
                        <script type="text/javascript" src="/js/base.js"></script>
                    </Head>

                    <a href="/" className={s.qinWrapper}>
                        <img className={s.qin} src="/images/qin.svg" alt="" />
                    </a>



                    {/* nav for desktop only */}
                    <nav className={s.nav}>
                        <ul className={s.menu}>
                            {
                                menu.map((v, i) => (
                                    <a key={i}
                                        href={v === "home" ? "/" : (v === "cv" ? `/cv.pdf` : `/${v}`)}
                                        target={v === "cv" ? "_blank" : ""}>
                                        <li className={clsConcat(s.menuItem, this.props.active === v ? s.menuItemActive : "")}>{v}</li>
                                    </a>
                                ))
                            }
                        </ul>
                    </nav>

                    {/* bars for mobile */}
                    <div className={s.bar} onClick={this.onMenuIconClick}>
                        <FontAwesomeIcon icon={faBars} />
                    </div>
                </header>
            </>
        )
    }
}

export default Header
